body, html {
  margin: 0px;
  width: 100vw;
  height: 100vh;
  padding: 0px;
  max-width: 800px;
  margin: auto;
  scroll-behavior: smooth;
}

body {
  font-family: 'poppins-medium';
  /* background-color: rgb(252, 252, 252); */
  /* color: rgb(29, 29, 29); */
  color: rgb(250, 250, 250);
  background-color: rgb(29, 29, 29);

  background-size: cover;
  background-position: center center;
}

::selection {
    /* background: rgb(120, 201, 255); */
    background: rgb(11, 98, 230);
}

/* Change cursor color */

/* Make scroll bar invisible */
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background: transparent;
}

/* Removes blue border of selected buttons */
button:focus { outline: 0; }
div:focus { outline: 0; }
span:focus { outline: 0; }
img:focus { outline: 0; }
textarea:focus { outline: 0; }
p:focus { outline: 0; }
input:focus { outline: 0; }
select:focus { outline: 0; }
app-arrow-button:focus { outline: 0; }

