/* Transforms */
.transform-color {
  -webkit-transition: color 0.2s;
  -moz-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
}

.transform-border-radius {
  -webkit-transition: border-radius 0.2s;
  -moz-transition: border-radius 0.2s;
  -o-transition: border-radius 0.2s;
  transition: border-radius 0.2s;
}

.transform-background-color {
  -webkit-transition: background-color 0.2s;
  -moz-transition: background-color 0.2s;
  -o-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.transform-width {
  -webkit-transition: width 0.2s;
  -moz-transition: width 0.2s;
  -o-transition: width 0.2s;
  transition: width 0.2s;
}

.transform-border {
  -webkit-transition: border 0.2s;
  -moz-transition: border 0.2s;
  -o-transition: border 0.2s;
  transition: border 0.2s;
}

.tranform-blur {
  -webkit-transition: filter 0.2s;
  -moz-transition: filter 0.2s;
  -o-transition: filter 0.2s;
  transition: filter 0.2s;
}

.tranform-webkit-filter {
  -webkit-transition: -webkit-filter 0.2s;
  -moz-transition: -webkit-filter 0.2s;
  -o-transition: -webkit-filter 0.2s;
  transition: -webkit-filter 0.2s;
}

.transform-height {
  -webkit-transition: height 0.2s;
  -moz-transition: height 0.2s;
  -o-transition: height 0.2s;
  transition: height 0.2s;
}

.transform-opacity {
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.transform-line-height {
  -webkit-transition: line-height 0.2s;
  -moz-transition: line-height 0.2s;
  -o-transition: line-height 0.2s;
  transition: line-height 0.2s;
}

.transform-font-size {
  -webkit-transition: font-size 0.2s;
  -moz-transition: font-size 0.2s;
  -o-transition: font-size 0.2s;
  transition: font-size 0.2s;
}

.transform-margin {
  -webkit-transition: margin 0.2s;
  -moz-transition: margin 0.2s;
  -o-transition: margin 0.2s;
  transition: margin 0.2s;
}

.transform-box-shadow {
    -webkit-transition: box-shadow 0.2s;
  -moz-transition: box-shadow 0.2s;
  -o-transition: box-shadow 0.2s;
  transition: box-shadow 0.2s;
}

/* Remove default style from links */
a:link { text-decoration: none; }
a:visited { text-decoration: none; }
a:hover { text-decoration: none; }
a:active { text-decoration: none; }

/* Raised Effect */
.raised {
  box-shadow: 
  inset 0 -3em 3em rgba(0,0,0,0.1), 
        /* 0 0  0 2px rgb(255,255,255), */
        0.3em 0.3em 1em rgba(0,0,0,0.3); 
}

/* Colors */
.text-orange { color: rgb(255, 102, 0); }
.text-green { color: rgb(1, 218, 1); }
.text-blue { color: rgb(43, 165, 247); }
.text-red { color: rgb(247, 67, 67); }
.text-light { color: rgb(150, 148, 148); }